@import url(https://fonts.googleapis.com/css?family=Raleway:400,500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{box-sizing:border-box;font-family:"Raleway", sans-serif}#quote-box{background:white;color:black;border-radius:4px;width:550px;min-height:10rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;padding:3rem}#quote-box #text{font-size:2rem;margin-bottom:10px;font-weight:500;transition:2s}#quote-box #author{text-align:right;font-size:15px;font-weight:lighter;transition:2s}#quotations{font-size:1.8rem}.App-header{min-height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;font-size:calc(10px + 2vmin);color:white;transition:2s}.buttons{min-height:20px;margin-top:20px}.buttons #tweet-quote{float:left;font-size:17px;text-decoration:none;background:#4CAF50;color:white;padding:10px 10px 3px 10px;text-align:center;padding-top:11.5px;border-radius:3px;cursor:pointer;transition:2s}.buttons #new-quote{float:right;text-align:center;text-decoration:none;border:none;color:white;background-color:#4CAF50;outline:none;font-size:.9rem;border-radius:3px;padding:9px 18px 9px 18px;cursor:pointer;transition:2s}

